import Vue from 'vue'
import ElementUI from '../node_modules/element-ui'
// import '../node_modules/element-ui/lib/theme-chalk/index.css'
import App from './entranceObject_App'
import VueCookies from 'vue-cookies'
const globalFormatTable = require('./js/coreFramework.js').coreFramework.globalFormatTable
Vue.prototype.GLOBALFORMAT = globalFormatTable();
Vue.use(ElementUI)
Vue.config.productionTip = false
Vue.use(VueCookies)
/* eslint-disable no-new */
new Vue({
  el: '#app',
  components: { App },
  template: '<App/>'
})



