<template>
  <div class="bg-light text-center rounded p-4 mx-auto w-100"  style="max-width: 1200px;min-height: 300px;">
    <div v-if="state=='finished'">
      <img src="static/images/icon-success.svg" alt="">
      <h4 class="font-weight-bold text-info">Congratulations! Your video has been converted successfully</h4>
    </div>
    <div v-else-if="state=='convert'">
      <h4 class="font-weight-bold text-info">Audio conversion is processing:</h4>
    </div>
    <div v-else>
      <div class="text-info"><h4 class="mediaName">{{mediaName}}</h4></div>
      <div class="row justify-content-center small text-left text-lg-center">
        <div class="col-lg-2 col-6">Duration: {{fileInfo.duration}}</div>
        <div class="col-lg-2 col-6">Size: {{fileInfo.size}}</div>
      </div>
    </div>

    <div class="d-flex justify-content-center align-items-center mx-auto py-4">
      <div class="text-center">
        <div><img id="addicon" src="../assets/audio.svg"/></div>
      </div>
      <div class="pl-5" v-if="state=='finished'">
        <ul class="list-default">
          <li class="outputFileName">{{outputFile.name}}</li>
          <li>Format: {{this.mediaTask.OutFormat}}</li>
          <li>Size: {{ outputFile.fileSize || 'Original'}}</li>
          <li v-show="this.$refs.convertVue.editType_copy!=='AC'">Resolution: {{this.mediaTask.outputConfig.Resolution.Width + '*' + this.mediaTask.outputConfig.Resolution.Height}}</li>
          <li v-show="this.$refs.convertVue.editType_copy==='AC'">Audio Bitrate: {{(this.mediaTask.outputConfig.AudioBitrate || this.mediaTask.AudioBit) + ' kbps'}}</li>
        </ul>
      </div>
    </div>
    <div class="my-4" v-if="state==='convert'">
      <div class="mx-auto position-relative mb-1" style="border:solid 3px #02367d;height: 20px;border-radius: 100px;overflow: hidden;max-width: 400px;">
        <div class="progress-bar" :style="{'width': convertProgressNum+'%'}"></div>
      </div>
      <div class="small font-weight-bold text-info">{{convertProgressNum}}%</div>
    </div>
    <div id="videoEditor" class="mx-auto mb-3" style="max-width: 720px;" v-show="state=='edit'">
      <Compress v-if="hackReset" :media-task="mediaTask" :edit-type="'ACP'" ref="CompressVue"/>
    </div>
    <div v-show="state!='finished'">
      <div class="py-3">
        <a href="javascript:;" class="btn btn-info mx-2" @click="startConverter()" v-show="state!='convert'">Start Now</a>
        <a href="javascript:;" class="btn btn-outline-info mx-2" @click="onContinueClick()" v-show="state!='convert'">Cancel</a>
        <a href="javascript:;" v-if="state == 'convert'" class="btn btn-outline-info mx-2" @click="cancelConverter()">Cancel</a>
      </div>
    </div>
    <div class="py-4" v-show="state=='finished'">
      <a v-bind:href="outputFile.src" v-bind:download="outputFile.name" class="btn btn-info m-2"><span class="px-4">Download Now</span></a>
      <a href="javascript:;" @click="onContinueClick()" class="btn btn-outline-info m-2">Convert Again</a>
    </div>
  </div>
</template>

<script>
  import {GetDisplayTimeString, renderSize, getType} from "../js/util";
  import Converter from "./Converter";
  import Trim from "./Trim";
  import Crop from "./Crop";
  import Adjust from "./Adjust";
  import Rotate from "./Rotate";
  import Flip from "./Flip";
  import CropControl from './CropControl'
  import Compress from "./Compress";

  export default {
    name: "AudioCompress",
    components: {Compress},
    data() {
      return {
        curTabPage: 'convertTab',
        mediaName: "",
        fileInfo:"",
        converting: false,
        nProgress: 0,
        state: 'edit', //edit, convert, finished
        videoDuration: 0,
        outputFile: {
          src: '',
          name: '',
          fileSize: ''
        },
        hackReset: true
      }
    },
    props: ["mediaTask", "ShowConvertFailInfo"],

    mounted(){
       this.mediaTask.renderCanvas = this.$refs.imgCanvas;
     },

    computed: {
      canvasWidth: function () {
        return this.$refs.imgCanvas.clientWidth;
      },
      imageThumb() {
        return this.mediaTask.imageThumb;
      },
      media() {
        return this.mediaTask.media;
      }
    },

    watch:{
      imageThumb(curVal, oldVal){
        if(curVal){
          this.draw();
        }
      },
      media(curVal,oldVal){
        if(curVal)
          this.mediaName = curVal.FileOrBlob.name;
      },
      nProgress(curVal,oldVal){
        if(curVal < oldVal) return;
        if(curVal <= 1){
          this.$refs.convertProgress.style.width = ("{0}%").format(Math.floor(curVal*100));
        }
      },
    },
    methods: {
      fillFileInfo(){
        this.mediaName = this.mediaTask.media.FileOrBlob.name;
        this.videoDuration = this.mediaTask.media.MediaInfo.Duration;
        this.fileInfo = {
          fileType: getType(this.mediaName),
          size: renderSize(this.mediaTask.Size),
          duration: GetDisplayTimeString(this.mediaTask.Duration),
          width: this.mediaTask.Width,
          height: this.mediaTask.Height
        }
        this.$refs.CompressVue.formatValue = "MP3";
      },

      convertProgress(progress) {
        // console.log(progress)
        this.nProgress = progress;
      },

      startConverter(){
        this.state = 'convert';
        this.$refs.convertProgress.style.width = 0;
        this.nProgress = 0;
        this.mediaTask.encodeParamsCheck = this.GLOBALFORMAT.g_EncodeParamsCheck;
        this.mediaTask.startConverter(this.convertFinished, this.convertProgress);
      },

      convertFinished(files){
        if(files.length > 0) {
          let file = files[0];
          this.outputFile = {
            name: file.name,
            src: file.src,
            fileSize: renderSize(file.fileSize)
          };
          this.state = 'finished';
        }
        else{
          this.$emit("ShowConvertFailInfo");
          this.cancelCallBack('confirm');
        }
      },
      downloadFinishedFile(){
          this.$refs.convertedFile.click();
      },
      onContinueClick(){
        this.reset();
        this.$emit("addNewFile");
      },
      cancelConverter() {
        if(this.state=='convert') {
          swal({
            text: 'Do you want to cancel this file? The changes won\'t be applied if you cancel this page?',
            buttons: {
              yes: {
                text: 'Yes'
              },
              no: {
                text: 'No'
              }
            }
          }).then(function(value){
            if(value == 'yes'){
              this.cancelCallBack('confirm')
            }
          }.bind(this))
        }else{
          this.onContinueClick();
        }
      },

      cancelCallBack(action){
        if (action === "confirm") {
          this.state = 'edit';
          this.nProgress = 0;
          this.mediaTask.CancelConvert();
          //this.onContinueClick();
        }
      },

      reset(){
        this.curTabPage = 'convertTab';
        this.converting = false;
        this.nProgress = 0;
        this.state = 'edit';
        this.videoDuration = 0;
        this.mediaTask.ResetCrop();
        this.$refs.CompressVue.reset();
        this.hackReset = false
        this.$nextTick(() => {
          this.hackReset = true
        })
      },

      resetClick(){

      },
      draw() {
        this.mediaTask.render();
      },
    }
  }
</script>



