<template>
    <div class="p-3 pt-3 mt-3 position-relative" style="background-color: rebeccapurple;">
        <div class="position-absolute px-2 py-1" style="left: 0;top: -26px;background-color: rebeccapurple;">
            <div class="small-xs font-weight-light text-light">{{formatTime(trimStartTime)}}/{{formatTime(trimEndTime||mediaTask.Duration)}}</div>
        </div>
        <div id="filmstrip" ref="filmstrip" @mousemove="mousemove" @mouseup="trim_mouseup" class="strip">
            <!-- 底图 -->
            <div id="back_image" class="filmstrip_container">
                <img id="imageTrim" class="film-strip" v-show="imgSrc!==''" v-bind:src="imgSrc">
            </div>
            <!-- 格子 -->
            <div id="slices" class="position-absolute w-100 h-100" style="left: 0;top: -24px;">
                <div class="row no-gutters text-light">
                    <div class="col" v-for="ts in time_slices">
                        <div class="row no-gutters w-100">
                            <div class="col" v-for="(item,index) in 11">
                                <div class="d-flex w-100 justify-content-center">
                                    <div class="border-left border-light" v-bind:style="{'height': index == 5 ? '6px' : '3px'}"></div>
                                </div>
                            </div>
                        </div>
                        <div class="small-xs">{{ts}}</div>
                    </div>
                </div>
            </div>
            <!-- 操作区域 -->
            <div class="bg-secondary position-absolute h-100" style="left: 0;top: 0;opacity: 0.25;" :style="{left: trim_start + '%', right: trim_end + '%'}"></div>
            <div class="scale" style="top: 0;left: 0;width: 1px;" :style="{left: needle_pos + '%'}" v-show="!start_mousedown && !end_mousedown"></div>
            <div id="start"  ref="start" class="scale-start" @mousedown="trim_mousedown" @mouseup="trim_mouseup" :style="{left: trim_start + '%'}"></div>
            <div id="end" ref="end" class="scale-end" @mousedown="trim_mousedown" @mouseup="trim_mouseup" :style="{right: trim_end + '%'}"></div>
        </div>
    </div>
</template>


<script>
    import Vue from 'vue'

    function padZero(s) {
        return s < 10 ? '0' + s: s;
    }

    export default {
        name: "Trim",
        data() {
            return {
                time_slices: ["00:00", "00:00", "00:00", "00:00", "00:00"],
                needle_pos: 0,
                trim_start: 0,
                trim_end: 0,
                trimStartTime: 0,
                trimEndTime: 0,
                start_mousedown: false,
                end_mousedown: false,
                seekPosition: 0,
                timerOut:0,
                imgSrc: ''
            }
        },
      computed:{
        imageWall(){
          return this.mediaTask.imageWall;
    },

    filmstripOffset(){
      return $("#filmstrip").offset()
    }
      },
        watch:{
            imageWall(curVal, oldVal) {
              if(curVal)
                this.imgSrc= curVal.src;
              else
                this.imgSrc = "";
            },

            videoDuration(curVal, oldVal) {
              this.initSlices();
	                  },

          seekPosition(curVal, oldVal){
              if(this.timerOut >1 ){
                this.mediaTask.applySeek(curVal);
              }
            this.timerOut =0;
          }
        },

        props:["videoDuration","mediaTask"],

        mounted() {
    this.initSlices();
            window.addEventListener("mouseup", this.trim_mouseup, false);
            setInterval(this.requestSeekposImage,500);
        },

        methods: {
    initSlices() {
      //分为6份
      let fstep = this.videoDuration / 5;

      for (let i = 1; i <= 5; i++) {
        Vue.set(
          this.time_slices,
          i - 1,
          this.formatTime((i - 1) * fstep + fstep / 2)
        );
      }
      this.time_slices;
    },
            trimStart(){
              return this.trimStartTime;
            },
            trimEnd(){
              return this.trimEndTime;
            },
            seekPosChanged(){
                if (this.videoDuration == undefined)
                    this.videoDuration = "0";
      this.seekPosition = (this.videoDuration * this.needle_pos) / 100;
            },

            requestSeekposImage(){
              this.timerOut += 1;
            },

            trimRangeChanged(){
                if (this.videoDuration == undefined)
                    this.videoDuration = "0";
                this.trimStartTime = this.videoDuration * this.trim_start / 100;
                this.trimEndTime = this.videoDuration * (100 - this.trim_end) / 100;
            },

            resetTrimValue(){
                this.trim_start = 0;
                this.trim_end = 0;
                this.needle_pos = 0;
                this.trimStartTime = 0;
                this.trimEndTime = 0;
            },

    mousemove(e) {
      //为防止在边界上拖出去不响应，在此亦处理
      if (this.start_mousedown) {
        this.start_mousemove(e);
        return;
      }

      if (this.end_mousedown) {
        this.end_mousemove(e);
        return;
      }

      //指针定位
      e = e || window.event;
      let fp = this.$refs.filmstrip.offsetParent;
      if (fp == null) return;

      let x = e.clientX - this.filmstripOffset.left;
      let w = this.$refs.filmstrip.offsetWidth;
      if (x >= 0 && x <= w) {
        this.needle_pos = (x * 100) / w;
      }
      this.seekPosChanged();
    },

    start_mousemove(e) {
      if (!this.start_mousedown) return;

      e = e || window.event;
      let x = e.clientX - this.filmstripOffset.left;
      let w = this.$refs.filmstrip.offsetWidth;
      if (x >= 0 && x <= this.$refs.end.offsetLeft + this.$refs.end.offsetWidth - 7){
        this.trim_start = (x * 100) / w;
      }
      this.trimRangeChanged();
    },

    end_mousemove(e) {
      if (!this.end_mousedown) return;

      e = e || window.event;
      let x = e.clientX - this.filmstripOffset.left;
      let w = this.$refs.filmstrip.offsetWidth;
      if (x >= this.$refs.start.offsetLeft + 7 && x <= w) {
        this.trim_end = 100 - (x * 100) / w;
      }
      this.trimRangeChanged();
    },

    trim_mousedown(e) {
      this.start_mousedown = e.target === this.$refs.start;
      this.end_mousedown = e.target === this.$refs.end;
    },

    trim_mouseup(e) {
      this.start_mousedown = false;
      this.end_mousedown = false;
    },

    formatTime(_seconds) {
      _seconds = parseInt(_seconds);
      let hours, mins, seconds;
      let result = "";
      seconds = parseInt(_seconds % 60);
      mins = parseInt((_seconds % 3600) / 60);
      hours = parseInt(_seconds / 3600);

      if (hours)
        result = padZero(hours) + ":" + padZero(mins) + ":" + padZero(seconds);
      else result = padZero(mins) + ":" + padZero(seconds);
      return result;
    }
  }
};
</script>

<style type="text/css">
    .scale {
        position: absolute;left: 0;top: 0;width: 2px;height: 100%;background-color: #ff6500;-webkit-user-select:none;-moz-user-select:none;
    }
    .scale:before {
        content: '';width: 10px;height: 8px;background-color: #ff6500;position: absolute;left: 50%;top: -13px;transform: translateX(-50%);-webkit-user-select:none;-moz-user-select:none;
    }
    .scale:after {
        content: '';width: 0px;height: 0px;position: absolute;left: 50%;top: -5px;transform: translateX(-50%);-webkit-user-select:none;-moz-user-select:none;
        border-top: solid 5px #ff6500;border-left: solid transparent 5px;border-right: solid transparent 5px;-webkit-user-select:none;-moz-user-select:none;
    }
    .scale-start {
        position: absolute;left: 0;top: 0;width: 2px;height: 100%;background-color: blueviolet;cursor: col-resize;-webkit-user-select:none;-moz-user-select:none;
    }
    .scale-end {
        position: absolute;right: 0;top: 0;width: 2px;height: 100%;background-color: blueviolet;cursor: col-resize;-webkit-user-select:none;-moz-user-select:none;
    }
    .scale-start:before, .scale-end:before {
        content: '';width: 7px;height: 70%;background-color: blueviolet;position: absolute;left: 50%;transform: translateX(-50%);top: 15%;border-radius: 7px;-webkit-user-select:none;-moz-user-select:none;
    }



    .strip {
        user-select: none;
        position: relative;
        width: 100%;
        background: #fff;
        height: 56px;
    }
    .strip   .filmstrip_container {
        overflow: hidden;
        height: 100%;
        width: 100%;
    }
    .strip   .filmstrip_container   .film-strip {
        height: 100%;
        object-fit: contain;
    }
    .strip   .step-wrapper {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
    .strip   .step-wrapper.with-film-strip {
        color: #fff;
    }
    .strip   .step-wrapper.with-film-strip   .step:before {
        border-left: 1px solid #fff;
    }
    .strip   .step-wrapper   .step {
        position: relative;
        text-align: center;
        width: 20%;
        line-height: 2.75rem;
        font-size: 12px;
        float: left;
    }
    .strip   .step-wrapper   .step:before {
        content: '';
        position: absolute;
        left: 50%;
        top: 0;
        bottom: 75%;
        display: block;
        border-left: 1px solid #555555;
    }
    .strip   .selection {
        background:blueviolet;
        position: absolute;
        top: 0;
        bottom: 0;
        opacity: .2;
    }
</style>



