<template>
    <div>
      <div class="d-flex justify-content-center">
        <div class="mx-4 d-flex align-items-center">
          <span class="mr-3">Vertical: </span>
          <a href="javascript:;" v-on:click="vFlipClick"><img class="hover-hide" src="../assets/flip-h.svg" alt=""><img class="hover-show" src="../assets/flip-h-hover.svg" alt=""></a>
        </div>
        <div class="mx-4 d-flex align-items-center">
          <span class="mr-3">Horizontal: </span>
          <a href="javascript:;" v-on:click="hFlipClick"><img class="hover-hide" src="../assets/flip-v.svg" alt=""><img class="hover-show" src="../assets/flip-v-hover.svg" alt=""></a>
        </div>
      </div>
    </div>
</template>

<script>
    export default {
        name: "Flip",
        data(){
          return{
            title: 'this is Flip component!',
            vFlip: false,
            hFlip: false
          }
        },
      props:["mediaTask"],
      methods:{
          vFlipClick()
          {
            this.vFlip = !this.vFlip;
            this.mediaTask.applyFlipEffect(this.vFlip, this.hFlip);
          },
        hFlipClick()
        {
          this.hFlip = !this.hFlip;
          this.mediaTask.applyFlipEffect(this.vFlip, this.hFlip);
        },
        resetFlipValue(){
          this.vFlip = false;
          this.hFlip = false;
          this.mediaTask.applyFlipEffect(this.vFlip, this.hFlip);
        }
      }
    }
</script>

<style scoped>
  .button{
    width: 100px;
    height: 100px;
  }
</style>



