<template>
  <div id="cropArea" v-on:resize="onload" v-on:mousedown.top="onmousedown_Drag" style="max-width: 640px;max-height: 360px;" ref="cropAreaDiv">
    <div class="translate-center w-100 h-100 d-flex flex-wrap">
      <div class="border border-light" v-for="(item,index) in 18" style="width: 16.666%;height: 33.333%;"></div>
    </div>
    <div id="sizecontrol">
      <!--上-->
      <a id="topEle" v-on:mousedown.stop="onmousedown_Resize" class="resizeCommon resizeTop"></a>
      <!--下-->
      <a id="bottomEle" v-on:mousedown.stop="onmousedown_Resize" class="resizeCommon resizeBottom"></a>
      <!--左-->
      <a id="leftEle" v-on:mousedown.stop="onmousedown_Resize" class="resizeCommon resizeLeft"></a>
      <!--右-->
      <a  id="rightEle" v-on:mousedown.stop="onmousedown_Resize" class="resizeCommon resizeRight"></a>
      <!--左上-->
      <a id="lefttopEle" v-on:mousedown.stop="onmousedown_Resize" class="resizeCommon resizeLeftTop"></a>
      <!--右上-->
      <a id="righttopEle" v-on:mousedown.stop="onmousedown_Resize" class="resizeCommon resizeRigthTop"></a>
      <!--右下-->
      <a id="rightbottomEle" v-on:mousedown.stop="onmousedown_Resize" class="resizeCommon resizeRightBottom"></a>
      <!--左下-->
      <a id="leftbottomEle" v-on:mousedown.stop="onmousedown_Resize" class="resizeCommon resizeLeftBottom"></a>
      <div id="currentSize" class="sizeDiv">{{this.mediaTask.cropWidth +"x" + this.mediaTask.cropHeight}}</div>
    </div>
    <div id="buttonArea" class="cropControlButtonDiv">
        <button type="button" class="cropControlButtonCancel" v-on:click="onCropCanceled"/>
        <button type="button" class="cropControlButtonSelected" v-on:click="onCropSelected"/>
    </div>
  </div>
</template>

<script>
export default {
  name: "CropControl",
  data() {
    return {
      cropTop: 0,
      cropLeft: 0,
      cropWidth: 0,
      cropHeight: 0,
    }
  },
  props:["mediaTask","ResetCrop"],
  methods:{
    onmousedown_Drag: function(event){
         //debugger
         let divEle = document.getElementById('cropArea');
         let mousedownleft = event.clientX;
         let mousedowntop = event.clientY;
         let oldOffsetX = divEle.offsetLeft;
         let oldOffSetY = divEle.offsetTop;
         divEle.onmousemove = function(event){
            let divEle = document.getElementById('cropArea');
            //debugger
            let currentMouseX = event.clientX;
            let currentMouseY = event.clientY;
            let currentDivLeft = oldOffsetX + currentMouseX - mousedownleft;
            let currentDivTop = oldOffSetY + currentMouseY -  mousedowntop;
            if(currentDivLeft + divEle.offsetWidth > divEle.parentElement.offsetWidth){
              currentDivLeft = divEle.parentElement.offsetWidth - divEle.offsetWidth;
            }
            if(currentDivLeft < 0){
              currentDivLeft = 0;
            }
            if(currentDivTop + divEle.offsetHeight > divEle.parentElement.offsetHeight){
              currentDivTop = divEle.parentElement.offsetHeight - divEle.offsetHeight;
            }
            if(currentDivTop <0){
              currentDivTop = 0;
            }
            divEle.style.left = (currentDivLeft /divEle.parentElement.offsetWidth) * 100 + "%";
            divEle.style.top = (currentDivTop / divEle.parentElement.offsetHeight) * 100 + "%";
         };
         document.onmouseup = function(event){
            divEle.onmousemove = null;
         };
         this.cropTop = divEle.offsetTop;
         this.cropLeft = divEle.offsetLeft;
         this.cropWidth = divEle.offsetWidth;
         this.cropHeight = divEle.offsetHeight;
         let currentSizeEle = document.getElementById('currentSize');
         if(currentSizeEle){
           currentSizeEle.innerText = divEle.offsetWidth + "x" + divEle.offsetHeight;
         }
    },
    onmousedown_Resize:function(event){
      //debugger
        let divEle = document.getElementById('cropArea');
        let downElement = event.srcElement;
        let oldMouseX = event.clientX;
        let oldMouseY = event.clientY;
        let originWidth = divEle.offsetWidth;
        let originHeight = divEle.offsetHeight;
        let originLeft = divEle.offsetLeft;
        let originTop = divEle.offsetTop;
        this.isDragMouseDown = false;
        document.onmousemove = function(event){
            let mousemovedWidth = event.clientX - oldMouseX;
            let mousemovedHeight= event.clientY - oldMouseY;
            let totalWidth = originWidth + mousemovedWidth;
            let totalHeight = originHeight + mousemovedHeight;
            if(downElement.id=="topEle"){
                let currentTop =0;
                if(originHeight - mousemovedHeight <100){
                   currentTop = originTop +(originHeight- 100);
                   totalHeight =100;
                }
                else{
                   currentTop = originTop + mousemovedHeight;
                   totalHeight = originHeight - mousemovedHeight;
                }
                if(currentTop<0){
                    currentTop = 0;
                    totalHeight = originHeight + originTop;
                }
                //控制下边界，拉伸的时候保证下边界不伸出去
                if(currentTop + totalHeight > divEle.parentElement.offsetHeight){
                    totalHeight = divEle.parentElement.offsetHeight - currentTop;
                }
                divEle.style.top = currentTop +'px';
                divEle.style.height = totalHeight +'px';
            }
            if(downElement.id=="bottomEle"){
                // let totalHeight = originHeight + event.clientY - oldMouseY;
                if(totalHeight + divEle.offsetTop > divEle.parentElement.offsetHeight){
                    totalHeight = divEle.parentElement.offsetHeight - divEle.offsetTop;
                }
                if(totalHeight <100){
                    totalHeight = 100;
                }
                divEle.style.height = totalHeight +'px';
            }
            if(downElement.id=="leftEle"){
                let currentLeft =0;
                if(originWidth - mousemovedWidth <100){
                   currentLeft = originLeft +(originWidth- 100);
                   totalWidth =100;
                }
                else{
                   currentLeft = originLeft + mousemovedWidth;
                   totalWidth = originWidth - mousemovedWidth;
                }
                if(currentLeft<0){
                    currentLeft = 0;
                    totalWidth = originWidth + originLeft;
                }
                //控制右边界，保证往左拉伸不会伸出去
                if(currentLeft + totalWidth > divEle.parentElement.offsetWidth){
                    totalWidth = divEle.parentElement.offsetWidth - currentLeft;
                }
                divEle.style.left = currentLeft +'px';
                divEle.style.width = totalWidth +'px';
            }
            if(downElement.id=="rightEle"){
                if(totalWidth + divEle.offsetLeft > divEle.parentElement.offsetWidth){
                      totalWidth = divEle.parentElement.offsetWidth - divEle.offsetLeft;
                }
                if(totalWidth <100){
                    totalWidth = 100;
                }
                divEle.style.width = totalWidth +'px';
            }
            if(downElement.id=="lefttopEle"){
                let currentLeft =0;
                if(originWidth - mousemovedWidth <100){
                   currentLeft = originLeft +(originWidth- 100);
                   totalWidth =100;
                }
                else{
                   currentLeft = originLeft + mousemovedWidth;
                   totalWidth = originWidth - mousemovedWidth;
                }
                if(currentLeft<0){
                    currentLeft = 0;
                    totalWidth = originWidth + originLeft;
                }
                //控制右边界，保证往左拉伸不会伸出去
                if(currentLeft + totalWidth > divEle.parentElement.offsetWidth){
                    totalWidth = divEle.parentElement.offsetWidth - currentLeft;
                }
                let currentTop =0;
                if(originHeight - mousemovedHeight <100){
                   currentTop = originTop +(originHeight- 100);
                   totalHeight =100;
                }
                else{
                   currentTop = originTop + mousemovedHeight;
                   totalHeight = originHeight - mousemovedHeight;
                }
                if(currentTop<0){
                    currentTop = 0;
                    totalHeight = originHeight + originTop;
                }
                //控制下边界，拉伸的时候保证下边界不伸出去
                if(currentTop + totalHeight > divEle.parentElement.offsetHeight){
                    totalHeight = divEle.parentElement.offsetHeight - currentTop;
                }
                divEle.style.top = currentTop +'px';
                divEle.style.height = totalHeight +'px';
                divEle.style.left = currentLeft +'px';
                divEle.style.width = totalWidth +'px';
            }
            if(downElement.id=="righttopEle"){
                let currentTop =0;
                if(originHeight - mousemovedHeight <100){
                  currentTop = originTop +(originHeight- 100);
                  totalHeight =100;
                }
                else{
                   currentTop = originTop + mousemovedHeight;
                   totalHeight = originHeight - mousemovedHeight;
                }
                if(currentTop<0){
                    currentTop = 0;
                    totalHeight = originHeight + originTop;
                }
                //控制下边界，拉伸的时候保证下边界不伸出去
                if(currentTop + totalHeight > divEle.parentElement.offsetHeight){
                    totalHeight = divEle.parentElement.offsetHeight - currentTop;
                }
                if(totalWidth + divEle.offsetLeft > divEle.parentElement.offsetWidth){
                    totalWidth = divEle.parentElement.offsetWidth - divEle.offsetLeft;
                }
                if(totalWidth <100){
                    totalWidth = 100;
                }
                divEle.style.width = totalWidth +'px';
                divEle.style.top = currentTop +'px';
                divEle.style.height = totalHeight +'px';
            }
            if(downElement.id=="rightbottomEle"){
                // debugger
                if(totalWidth + divEle.offsetLeft > divEle.parentElement.offsetWidth){
                    totalWidth = divEle.parentElement.offsetWidth - divEle.offsetLeft;
                }
                if(totalHeight + divEle.offsetTop > divEle.parentElement.offsetHeight){
                    totalHeight = divEle.parentElement.offsetHeight - divEle.offsetTop;
                }
                if(totalWidth <100){
                    totalWidth = 100;
                }
                if(totalHeight <100){
                    totalHeight = 100;
                }
                divEle.style.width = totalWidth +'px';
                divEle.style.height = totalHeight +'px';
            }
            if(downElement.id=="leftbottomEle"){
                let currentLeft =0;
                if(originWidth - mousemovedWidth <100){
                   currentLeft = originLeft +(originWidth- 100);
                   totalWidth =100;
                }
                else{
                   currentLeft = originLeft + mousemovedWidth;
                   totalWidth = originWidth - mousemovedWidth;
                }
                if(currentLeft<0){
                    currentLeft = 0;
                    totalWidth = originWidth + originLeft;
                }
                //控制右边界，保证往左拉伸不会伸出去
                if(currentLeft + totalWidth > divEle.parentElement.offsetWidth){
                    totalWidth = divEle.parentElement.offsetWidth - currentLeft;
                }
                // let totalHeight = originHeight + event.clientY - oldMouseY;
                if(totalHeight + divEle.offsetTop > divEle.parentElement.offsetHeight){
                    totalHeight = divEle.parentElement.offsetHeight - divEle.offsetTop;
                }
                if(totalHeight <100){
                    totalHeight = 100;
                }
                divEle.style.height = totalHeight +'px';
                divEle.style.left = currentLeft +'px';
                divEle.style.width = totalWidth +'px';
            }
            let currentSizeEle = document.getElementById('currentSize');
            //debugger
            if(currentSizeEle){
              currentSizeEle.innerText = divEle.offsetWidth + "x" + divEle.offsetHeight;
            }
        };
        document.onmouseup = function(){
          //debugger
          document.onmousemove = null;
        };
        this.cropTop = divEle.offsetTop;
        this.cropLeft = divEle.offsetLeft;
        this.cropWidth = divEle.offsetWidth;
        this.cropHeight = divEle.offsetHeight;
    },

    onmouseleave_Resize:function(event){
        this.isResizeMouseDown = false;
    },

    onCropSelected: function(event){
      let cropArea = document.getElementById("cropArea");
      if(cropArea){
        document.getElementById("cropArea").style.visibility="hidden";
      }
      this.mediaTask.isCroping = false;
      this.mediaTask.ExcuteCrop(this.cropTop,this.cropLeft,this.cropWidth,this.cropHeight);
    },


    onCropCanceled(event){
      this.$emit("ResetCrop");
    },

    resetCropValue(){
      this.onCropCanceled();
      let cropArea = document.getElementById("cropArea");
      if(cropArea){
        document.getElementById("cropArea").style.visibility="";
      }
    },
    setCropAreaSize() {
      //debugger
      let divEle = document.getElementById('cropArea');
      let currentSizeEle = document.getElementById('currentSize');

      if (currentSizeEle) {
        currentSizeEle.innerText = divEle.offsetWidth > 0 ? divEle.offsetWidth + "x" + divEle.offsetHeight : this.mediaTask.cropWidth + "x" +this.mediaTask.cropHeight;
      }
    },
    onload() {
      let divEle = document.getElementById('cropArea');
      let currentSizeEle = document.getElementById('currentSize');

      if(currentSizeEle){
        currentSizeEle.innerText = divEle.offsetWidth + "x" + divEle.offsetHeight;
      }
    }
  }
}
</script>
<style scoped>
  #cropArea{
    position: absolute;
    top: 0;
    left: 0;
    width:100%;
    height:100%;
    background: rgb(0,0,0,0.4);
    opacity:0.4;
    border: 0px solid #fff;
    z-index:99;
    cursor: pointer;
  }
  .resizeCommon{
    position: absolute;
    z-index: 100;
    width: 1.25rem;
    height: 1.25rem;
  }
  .resizeTop{
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    border-top: 0.25rem solid #fff;
    cursor: ns-resize;
  }
  .resizeBottom{
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    border-bottom: 0.25rem solid #fff;
    cursor: ns-resize;
  }
  .resizeLeft{
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    border-left: 0.25rem solid #fff;
    cursor: ew-resize;
  }
  .resizeRight{
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    border-right: 0.25rem solid #fff;
    cursor: ew-resize;
  }
  .resizeLeftTop{
    top: 0;
    left: 0;
    transform: translateX(0);
    border-top: 0.25rem solid #fff;
    border-left: 0.25rem solid #fff;
    cursor: nwse-resize;
  }
  .resizeRigthTop{
    top: 0;
    right: 0;
    transform: translateX(0);
    border-top: 0.25rem solid #fff;
    border-right: 0.25rem solid #fff;
    cursor: nesw-resize;
  }
  .resizeRightBottom{
    bottom: 0;
    right: 0;
    transform: translateY(0);
    border-bottom: 0.25rem solid #fff;
    border-right: 0.25rem solid #fff;
    cursor: nwse-resize;
  }
  .resizeLeftBottom{
    bottom: 0;
    left: 0;
    transform: translateX(0);
    border-bottom: 0.25rem solid #fff;
    border-left: 0.25rem solid #fff;
    cursor: nesw-resize;
  }
  .cropControlButtonDiv{
    position: absolute;
    right: 5px;
    bottom: 30px;
    width: 75px;
    height: 40px;
  }
  .cropControlButtonCancel{
    width: 32px;
    height: 32px;
    background: url(../assets/cancel.png) no-repeat;
    border: 0 solid #fff;
  }
  .cropControlButtonCancel:hover{
    background: url(../assets/cancel_hover.png) no-repeat;
  }
  .cropControlButtonSelected{
    width: 32px;
    height: 32px;
    background: url(../assets/selected.png) no-repeat;
    border: 0 solid #fff;
  }
  .cropControlButtonSelected:hover{
    background: url(../assets/selected_hover.png) no-repeat;
  }
  .sizeDiv{
    position:absolute;
    bottom:1rem;
    right:1rem;
    background:rgba(0, 0, 0, 0.6);
    font-size:.75rem;color:#fff;
  }
</style>



