<template>
  <div class="mb-0">
    <div class="d-flex flex-column flex-lg-row justify-content-center align-items-center mb-4">
      <div class="m-2">Output quality:</div>
      <div class="dropdown m-2" style="min-width: 220px">
        <a href="javascript:;" class="dropdown-toggle" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
          {{compressQuality.desc+ '('+renderSize(compressQuality.value*mediaTask.Size)+')'}}
        </a>
        <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
          <a class="dropdown-item" v-for="item in compressQualityData" href="javascript:;" @click="compressQuality = item">{{item.desc+ '('+renderSize(item.value*mediaTask.Size)+')'}}</a>
        </div>
      </div>
      <!--<a href="javascript:;" @click="setting=!setting" class="m-2" v-show="showSetting"><img src="../assets/icon-setting.svg" alt=""></a>-->
    </div>
    <div class="border rounded pt-3 pb-3">
      <p class="text-info font-weight-bold">Advanced Settings (Optional):</p>
      <div class="row justify-content-center align-items-center">
        <div class="col-lg-5 py-2">
          <div class="d-flex align-items-center justify-content-lg-center justify-content-between">
            <div>Save Format:</div>
            <div class="dropdown mx-2">
              <a href="javascript:;" class="dropdown-toggle" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                {{formatValue}}
              </a>
              <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                <a class="dropdown-item" v-for="item in this.GLOBALFORMAT.g_EncodeParamsCheck.getFomats(getFormatType())" href="javascript:;" @click="formatValue = item">{{item.toString()}}</a>
              </div>
            </div>
          </div>
        </div>
        <div class="col-lg-5 py-2" v-if="editType == 'ACP'">
          <div class="d-flex align-items-center justify-content-lg-center justify-content-between">
            <div>Audio Bitrate:</div>
            <div class="dropdown mx-2">
              <a href="javascript:;" class="dropdown-toggle" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                {{audioBitValue ? audioBitValue + 'kbps' : 'Same as Source'}}
              </a>
              <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                <a class="dropdown-item" v-for="item in getAudioBits()" href="javascript:;" @click="audioBitValue = item.value">{{item.label}}</a>
              </div>
            </div>
          </div>
        </div>
        <div class="col-lg-5 py-2" v-else>
          <div class="d-flex align-items-center justify-content-lg-center justify-content-between">
            <div>Resolution:</div>
            <div class="dropdown mx-2">
              <a href="javascript:;" class="dropdown-toggle" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                {{resolutionValue || 'Same as Source'}}
              </a>
              <div class="dropdown-menu" aria-labelledby="dropdownMenuButton" style="min-width: 190px">
                <a class="dropdown-item" v-for="item in getResolutions()" href="javascript:;" @click="resolutionValue = item">{{item || 'Same as Source'}}</a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {renderSize} from "../js/util";
    export default {
        name: "Compress",
        data() {
          return {
            resolutionValue: null,
            audioBitValue: "",
            outSizeValue: '',
            formatValue: 'MP4',
            setting: false,
            compressQuality:{desc:"High",value:0.8},
            compressQualityData: [
              {desc:"High",value:0.8},
              {desc:"Medium",value:0.5},
              {desc:"Low",value:0.3},
            ]
          };
      },
      created(){
      },
        props: ["mediaTask","editType","showSetting"],
        watch:{
          resolutionValue(curVal, oldVal){
            this.mediaTask.resulotionValue = curVal;
          },
          formatValue(curVal, oldVal){
            this.mediaTask.OutFormat = curVal;
          },
          outSizeValue(curVal, oldVal) {
            this.mediaTask.outputSize = curVal;
          },
          audioBitValue(curVal, oldVal) {
            this.mediaTask.outputAudioBit = curVal;
          },
          compressQuality(curVal, oldVal){
            this.mediaTask.outputSize = curVal.value * this.mediaTask.Size / 1024 /1024 ; //换算成MB
            this.$emit('child-say', curVal.desc)
          }
        },
      methods:{
        getAudioBits(){
            // return this.GLOBALFORMAT.AudioBitRates;
            let arr = this.GLOBALFORMAT.AudioBitRates.map(function(value,index,array){ return {'label': value + ' kbps', 'value': value}; });
             console.log(arr)
            return arr;
          },
        getFormatType(){
          if(this.editType==='AC' || this.editType ==='ACP') {
            return "audio";
          }else{
            return "video";
          }
        },
        handleInput(e){
          this.outSizeValue=this.outSizeValue.replace(/[^\.\d]/g,'');
        },
        reset(){
          this.outSizeValue = null;
          this.formatValue = this.editType==='AC' || this.editType ==='ACP'? "MP3":"MP4";
        },
        renderSize(obj){
          return renderSize(obj)
        },
        getResolutions(){
          let arr = this.GLOBALFORMAT.g_EncodeParamsCheck.getResolutionList(this.formatValue)
          arr.unshift(null);
          [0].concat(arr);
          return arr;
        },
      }
    }
</script>

<style scoped>
  .labelnormalcss {
    font-size: 1rem;
    text-align: left;
    margin-right: 30px;
    width: 18%;
  }
  .videoEditoritem
  {
    display: flex;
    align-content: center;
    text-align: left;
    align-items: center;
    margin-top: 8px;
  }
  .selectcss{
    width: 40%;
  }
  .dropdown {
    min-width: 100px;border: solid 1px #979797;padding: 5px 10px;
  }
  .dropdown-toggle:hover {
    text-decoration: none;
  }
  .dropdown-menu {
    width: 100%;transform: none !important;top: 100% !important;border-radius: 0;
  }
  .dropdown-item:hover {
    background-color: #02367d;color: #fff;
  }
  .dropdown-toggle[aria-expanded="true"]::after {
    transform: rotate(180deg);
  }
</style>



