<template>
  <div>
    <FlieLoad :media-task="mediaTask" v-show="showFileAdd" :edit-type="editType||'VE'" @ShowLoadFailInfo="ShowLoadFailInfo" ref="fileLoadVue" />

    <video-convert v-if="editType==='VC'" :media-task="mediaTask" :edit-type="editType" @addNewFile="addNewFile" @ShowConvertFailInfo="ShowConvertFailInfo" v-show="showEditor" ref="videoConvertVue"></video-convert>
    <VideoEditor v-if="editType==='VE'" :media-task="mediaTask" :edit-type="editType" @addNewFile="addNewFile" @ShowConvertFailInfo="ShowConvertFailInfo" v-show="showEditor" ref="videoEditVue"/>
    <VideoCompress v-if="editType==='VCP'" :media-task="mediaTask" :edit-type="editType" @addNewFile="addNewFile" @ShowConvertFailInfo="ShowConvertFailInfo" v-show="showEditor" ref="videoCompressVue"></VideoCompress>
    <AudioConvert v-if="editType==='AC'" :media-task="mediaTask" @addNewFile="addNewFile" @ShowConvertFailInfo="ShowConvertFailInfo" v-show="showEditor" ref="audioConvertVue"></AudioConvert>
    <audio-compress v-if="editType==='ACP'":media-task="mediaTask" :edit-type="editType" @addNewFile="addNewFile" @ShowConvertFailInfo="ShowConvertFailInfo" v-show="showEditor" ref="AudioCompressVue"></audio-compress>
    <VideoTrim v-if="editType==='VTrim'":media-task="mediaTask" :edit-type="editType" @addNewFile="addNewFile" @ShowConvertFailInfo="ShowConvertFailInfo" v-show="showEditor" ref="VideoTrimVue"></VideoTrim>
    <VideoAdjust v-if="editType==='VAdjust'":media-task="mediaTask" :edit-type="editType" @addNewFile="addNewFile" @ShowConvertFailInfo="ShowConvertFailInfo" v-show="showEditor" ref="VideoAdjustVue"></VideoAdjust>
    <VideoRotate v-if="editType==='VRF'":media-task="mediaTask" :edit-type="editType" @addNewFile="addNewFile" @ShowConvertFailInfo="ShowConvertFailInfo" v-show="showEditor" ref="VideoRotateFlipVue"></VideoRotate>

    <div class="modal fade" id="Modal-loadFail" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog modal-lg modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-body px-lg-5 py-5">
            <h6 class="text-secondary font-weight-light">The file failed to be loaded. The reasons may be:</h6>
            <h4 class="font-weight-bold text-info mb-4">1. The file format is not supported;</h4>
            <h4 class="font-weight-bold text-info mb-4">2. The decoding of this file is failed.</h4>
            <div class="mb-4">
              <a href="/" @click="closeModal('#Modal-loadFail')" class="btn btn-lg btn-outline-info">Go to Homepage</a>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="modal fade" id="Modal-ConvertFail" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog modal-lg modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-body px-lg-5 py-5">
            <h6 class="text-secondary font-weight-light">Failed! The reasons may be:</h6>
            <h4 class="font-weight-bold text-info mb-4">1. Encoding of this file is failed.</h4>
            <h4 class="font-weight-bold text-info mb-4">2. Some codecs inside a file format container that is normally working can't be converted.</h4>
            <div class="mb-4">
              <a href="/" @click="closeModal('#Modal-ConvertFail')" class="btn btn-lg btn-outline-info">Go to Homepage</a>
            </div>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
  import FlieLoad from "./FileLoad";
  import VideoEditor from "./VideoEditor";
  import {getType, sendGa} from "../js/util";
  // import { setLocatePath, MediaTask } from 'mediaFramework';
  const setLocatePath = require('../js/coreFramework.js').coreFramework.setLocatePath
  const MediaTask = require('../js/coreFramework.js').coreFramework.MediaTask
  import VideoConvert from "./VideoConvert";
  import VideoCompress from "./VideoCompress";
  import AudioConvert from "./AudioConvert";
  import AudioCompress from "./AudioCompress";
  import VideoTrim from "./VideoTrim";
  import VideoAdjust from "./VideoAdjust";
  import VideoRotate from "./VideoRotate";
  export default {
    name: "MainContent",
    components: {
      VideoRotate,
      VideoAdjust, VideoTrim, AudioCompress, AudioConvert,VideoCompress, VideoConvert, VideoEditor, FlieLoad},
    data() {
      return {
        mediaTask: new MediaTask(),
        showFileAdd: true,
        showEditor: false,
        editType: "VC"
      };
    },
    created(){
      this.editType = document.getElementById('app').getAttribute('data-editType') || this.editType;
      this.mediaTask.onLoadComplate = this.loadComplete;
    },
    watch:{
      editType(curVal, oldVal) {
        this.addNewFile();
      }
    },
    methods:{
      loadComplete(loadResult) {
        if (loadResult) {
          this.showEditor = true;
          this.showFileAdd = false;
          switch (this.editType) {
            case 'VC':
               this.$refs.videoConvertVue.fillFileInfo();
              break;
            case 'VE':
              this.$refs.videoEditVue.fillFileInfo();
              break;
            case 'VCP':
              this.$refs.videoCompressVue.fillFileInfo();
              break;
            case 'AC':
              this.$refs.audioConvertVue.fillFileInfo();
              break;
            case 'ACP':
              this.$refs.AudioCompressVue.fillFileInfo();
              break;
            case "VTrim":
              this.$refs.VideoTrimVue.fillFileInfo();
              break;
            case "VAdjust":
              this.$refs.VideoAdjustVue.fillFileInfo();
              break;
            case "VRF":
              this.$refs.VideoRotateFlipVue.fillFileInfo();
              break;
          }
        } else {
          this.ShowLoadFailInfo()
        }
      },
      ShowLoadFailInfo() {
        $('#Modal-loadFail').modal();
        this.addNewFile()
      },

      ShowConvertFailInfo() {
        $('#Modal-ConvertFail').modal();
      },


      closeModal(str){
        $(str).modal('hide')
      },

      addNewFile() {
        if (this.mediaTask)
          this.mediaTask.reset();
        this.showFileAdd = true;
        this.showEditor = false;
        this.$refs.fileLoadVue.reset();
      }
    }
  }
</script>

<style scoped>

</style>



