<template>
    <div class="row">
      <div class="col-lg-3 py-2 mr-0 text-right"> Save Format:</div>
      <div class="col-lg-2 ml-0 py-2 dropdown  border border-gray97">
        <a href="javascript:;" class="text-left dropdown-toggle" id="dropdownMenuButtonVF" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
          {{formatValue}}
        </a>
        <div class="dropdown-menu" aria-labelledby="dropdownMenuButtonVF" style="min-width: 190px">
          <a class="dropdown-item" v-for="item in this.GLOBALFORMAT.g_EncodeParamsCheck.getFomats('video')" href="javascript:;"  @click="formatValue = item;">{{item}}</a>
        </div>
      </div>

      <div class="col-lg-2 py-2 ml-2 mr-0 text-right" >Resolution:</div>

      <div class="col-lg-3 py-2 dropdown border border-gray97">
        <a href="javascript:;" class="text-left dropdown-toggle" id="dropdownMenuButtonV" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
          {{resolutionValue || 'Same as Source'}}
        </a>
        <div class="dropdown-menu" aria-labelledby="dropdownMenuButtonV" style="min-width: 190px">
          <a class="dropdown-item" v-for="item in getResolutions()" href="javascript:;" @click="resolutionValue = item">{{item || 'Same as Source'}}</a>
        </div>
      </div>
    </div>
</template>
<script>
    export default {
        name: "ConvertSet",
        data() {
          return {
            resolutionValue: null,
            audioBitValue: '',
            formatValue: 'MP4',
          };
      },
        props: ["mediaTask","editType"],
        watch:{
          resolutionValue(curVal, oldVal){
            this.mediaTask.resulotionValue = curVal;
          },
          formatValue(curVal, oldVal){
            this.mediaTask.OutFormat = curVal;
          },
          audioBitValue(curVal, oldVal) {
            this.mediaTask.outputAudioBit = curVal;
          },
        },
      methods:{
        getResolutions(){
          let arr = this.GLOBALFORMAT.g_EncodeParamsCheck.getResolutionList(this.formatValue)
          arr.unshift(null);
          [0].concat(arr);
          return arr;
        },
      }
    }
</script>

<style scoped>
  .labelnormalcss {
    font-size: 1rem;
    text-align: left;
    margin-right: 30px;
    width: 18%;
  }
  .selectcss{
    width: 60%;
  }
  .dropdown, .dropright {
    min-width: 100px;
  }
  .dropdown-toggle:hover {
    text-decoration: none;
  }
  .dropdown .dropdown-menu {
    width: 100%;transform: none !important;top: 100% !important;border-radius: 0;min-width: 0;
  }
  .dropdown-item:hover {
    background-color: #02367d;color: #fff;
  }
</style>



