<template>
  <div class="container bg-light text-center rounded p-4 mt-2 mx-auto w-100">
    <div v-if="state=='finished'">
      <img src="static/images/icon-success.svg" alt="">
      <h4 class="font-weight-bold text-info">Congratulations! Your file is ready</h4>
    </div>
    <div v-else-if="state=='convert'">
      <h4 class="font-weight-bold text-info">Preparing your file:</h4>
    </div>
    <div v-else>
      <div class="text-info"><h4 class="mediaName">{{mediaName}}</h4></div>
      <div class="row justify-content-center small text-left text-lg-center">
        <div class="col-lg-2 col-6">Duration: {{fileInfo.duration}}</div>
        <div class="col-lg-2 col-6">Size: {{fileInfo.size}}</div>
        <div class="col-lg-2 col-12">Resolution: {{fileInfo.width + 'x' + fileInfo.height}}</div>
      </div>
    </div>

    <div class="row border rounded mt-2">
      <!--缩略图区域-->
      <div class="col-lg-6 col-md-6 col-sm-12 mt-1 mb-1 py-2 pb-2">
        <div class="canvasDiv" ref="canvasDiv">
          <div class="d-inline-block" style="display:table-cell; vertical-align: middle; text-align: center;">
            <div class="position-relative w-100" style="font-size: 0;left:0 !important; text-align:center;vertical-align: middle; display: inline-block;" v-show="isVideo">
              <canvas id="imageCanvas" ref="imgCanvas"></canvas>
              <CropControl :media-task="mediaTask" v-show="curTabPage=='cropTab'"  @ResetCrop="ResetCrop" ref="cropControlVue"/>
            </div>
          </div>
        </div>
      </div>

      <div class="col-lg-6 col-md-6 col-sm-12">
        <!--转换完成信息-->
        <div class="w-75 p-3 pl-5 mt-5 mb-3 text-center align-items-center justify-content-center border rounded " v-if="state=='finished'">
          <ul class="list-default">
            <li class="outputFileName">{{outputFile.name}}</li>
            <li>Format: {{this.mediaTask.OutFormat}}</li>
            <li>Size: {{ outputFile.fileSize || 'Original'}}</li>
            <li>Resolution: {{this.mediaTask.outputConfig.Resolution.Width + '*' + this.mediaTask.outputConfig.Resolution.Height}}</li>
          </ul>
        </div>
        <!--转换进度 -->
        <div class="w-100 d-flex flex-column justify-content-center align-items-center mb-0 pt-3" style="height: 60% " v-if="state==='convert'">
          <div class="progress progress-striped w-75 m-3" >
            <div class="progress-bar progress-bar-info" v-bind:style="{'width': convertProgressNum+'%'}" role="progressbar" aria-valuenow="40" aria-valuemax="100" aria-valuemin="0"></div>
          </div>
          <div class="mt-2 font-weight-bold text-info">{{convertProgressNum}}% Complete</div>
        </div>
        <!--编辑区域 -->
        <!--高级编辑-->
        <div id="videoEditor" class="w-100 mt-2 pt-3 mb-2" v-show="state=='edit'">
          <ul class="nav nav-pills justify-content-center mb-4" id="pills-tab" role="tablist">
            <li class="nav-item my-2 mx-1">
              <a class="nav-link active" id="pills-2-tab" data-toggle="pill" href="#pills-2" role="tab" aria-controls="pills-2" aria-selected="false" @click="curTabPage = 'trimTab'" ref="trimButton">Trim</a>
              <a href="javascript:;" class="reset-icon" v-if="curTabPage == 'trimTab'" @click="resetEdit()"><img src="../assets/reset.svg" alt=""></a>
            </li>
            <li class="nav-item my-2 mx-1">
              <a class="nav-link" id="pills-3-tab" data-toggle="pill" href="#pills-3" role="tab" aria-controls="pills-3" aria-selected="false" @click="curTabPage = 'cropTab'">Crop</a>
              <a href="javascript:;" class="reset-icon" v-if="curTabPage == 'cropTab'" @click="resetEdit()"><img src="../assets/reset.svg" alt=""></a>
            </li>
            <li class="nav-item my-2 mx-1">
              <a class="nav-link" id="pills-4-tab" data-toggle="pill" href="#pills-4" role="tab" aria-controls="pills-4" aria-selected="false" @click="curTabPage = 'adjustTab'">Adjust</a>
              <a href="javascript:;" class="reset-icon" v-if="curTabPage == 'adjustTab'" @click="resetEdit()"><img src="../assets/reset.svg" alt=""></a>
            </li>
            <li class="nav-item my-2 mx-1">
              <a class="nav-link" id="pills-5-tab" data-toggle="pill" href="#pills-5" role="tab" aria-controls="pills-5" aria-selected="false" @click="curTabPage = 'rotateTab'">Rotate</a>
              <a href="javascript:;" class="reset-icon" v-if="curTabPage == 'rotateTab'" @click="resetEdit()"><img src="../assets/reset.svg" alt=""></a>
            </li>
            <li class="nav-item my-2 mx-1">
              <a class="nav-link" id="pills-6-tab" data-toggle="pill" href="#pills-6" role="tab" aria-controls="pills-6" aria-selected="false" @click="curTabPage = 'flipTab'">Flip</a>
              <a href="javascript:;" class="reset-icon" v-if="curTabPage == 'flipTab'" @click="resetEdit()"><img src="../assets/reset.svg" alt=""></a>
            </li>
          </ul>
          <div class="tab-content" id="pills-tabContent">
            <!--<div class="tab-pane fade show active" id="pills-1" role="tabpanel" aria-labelledby="pills-1-tab"><Converter v-if="hackReset" :media-task="mediaTask" :edit-type="editType" ref="convertVue"/></div>-->
            <div class="tab-pane fade show active" id="pills-2" role="tabpanel" aria-labelledby="pills-2-tab"><Trim :media-task="mediaTask" :videoDuration="videoDuration" ref="trimVue"/></div>
            <div class="tab-pane fade" id="pills-3" role="tabpanel" aria-labelledby="pills-3-tab"><Crop :media-task="mediaTask" v-on:child-say="selectChangeCrop" ref="cropVue"/></div>
            <div class="tab-pane fade" id="pills-4" role="tabpanel" aria-labelledby="pills-4-tab"><Adjust :media-task="mediaTask" ref="adjustVue" /></div>
            <div class="tab-pane fade" id="pills-5" role="tabpanel" aria-labelledby="pills-5-tab"><Rotate :media-task="mediaTask" ref="rotateVue"/></div>
            <div class="tab-pane fade" id="pills-6" role="tabpanel" aria-labelledby="pills-6-tab"><Flip :media-task="mediaTask" ref="FlipVue" /></div>
          </div>
        </div>
        <!--转换设置区域-->
        <div  class="w-100 mt-3 pt-2 mb-4" v-show="state=='edit'">
          <ConvertSet :media-task="mediaTask" v-if="hackReset" :edit-type="editType" ref="convertVue"/>
        </div>
        <!--控制按钮-->
        <div v-if="state!='finished'">
          <a href="javascript:;" class="btn btn-info mx-2" @click="startConverter()" v-show="state!='convert'">Start Now</a>
          <a href="javascript:;" class="btn btn-outline-info mx-2" @click="onContinueClick()" v-show="state!='convert'">Cancel</a>
          <a href="javascript:;" v-if="state == 'convert'" class="btn btn-outline-info mx-2" @click="cancelConverter()">Cancel</a>
        </div>
        <!--完成控制按钮-->
        <div class="w-75 py-1 mt-4 align-items-center justify-content-center" v-show="state=='finished'">
          <a v-bind:href="outputFile.src" v-bind:download="outputFile.name" class="btn btn-info m-2"><span>Download Now</span></a>
          <a href="javascript:;" @click="onContinueClick()" class="btn btn-outline-info m-2">Convert Another</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {GetDisplayTimeString, renderSize, getType} from "../js/util";
  import Converter from "./Converter";
  import Trim from "./Trim";
  import Crop from "./Crop";
  import Adjust from "./Adjust";
  import Rotate from "./Rotate";
  import Flip from "./Flip";
  import CropControl from './CropControl'
  import Compress from "./Compress";
  import ConvertSet from "./ConvertSet";

  export default {
    name: "VideoEditor",
    components: {ConvertSet, Compress, Flip, Rotate, Adjust, Crop, Trim, Converter,CropControl},
    data() {
      return {
        curTabPage: 'trimTab',
        mediaName: "",
        fileInfo:"",
        converting: false,
        nProgress: 0,
        state: 'edit', //edit, convert, finished
        videoDuration: 0,
        isVideo: true,
        convertProgressNum: 0,
        outputFile: {
          src: '',
          name: '',
          fileSize: ''
        },
        sropSelf: 'default',
        compressMsg: 'High',
        hackReset: true,
        start_startTime: 0,
        start_endTime: 0,
        cropWithInitValue: 0,
        toolType: "crop"
      }
    },
    props: ["mediaTask", "ShowConvertFailInfo", "editType"],
    mounted(){
      this.mediaTask.renderCanvas = this.$refs.imgCanvas;
     },

    computed: {
      canvasWidth: function () {
        return this.$refs.imgCanvas.clientWidth;
      },
      imageThumb() {
        return this.mediaTask.imageThumb;
      },
      media() {
        return this.mediaTask.media;
      }
    },

    watch:{
      imageThumb(curVal, oldVal){
        if(curVal){
          this.draw();
        }
      },
      cropWithInitValue(){
        this.$refs.cropControlVue.setCropAreaSize();
      },
      media(curVal,oldVal){
        if(curVal)
          this.mediaName = curVal.FileOrBlob.name;
      },
      nProgress(curVal,oldVal){
        if(curVal < oldVal) return;
        if(curVal <= 1){
          // this.$refs.convertProgress.style.width = ("{0}%").format(Math.floor(curVal*100));
          this.convertProgressNum = Math.floor(curVal*100)
        }
      },
      curTabPage(curVal, oldVal){
        if(curVal ==="cropTab"){
          this.mediaTask.isCroping =  document.getElementById("cropArea").style.visibility !=="hidden";
          if(this.cropWithInitValue==0)
            this.cropWithInitValue = this.mediaTask.previewWidth;
        }
        else {
          this.mediaTask.isCroping = false;
        }
        this.draw();
      }

    },
    methods: {
      // crop组件返回下拉框的组
      selectChangeCrop(msg) {
        this.sropSelf = msg
      },
      // compress组件返回下拉框的组
      selectChangeCompress(msg) {
        this.compressMsg = msg
      },
      fillFileInfo(){
        this.mediaName = this.mediaTask.FileName;
        this.videoDuration = this.mediaTask.Duration;
        this.fileInfo = {
          fileType: getType(this.mediaName),
          size: renderSize(this.mediaTask.Size),
          duration: GetDisplayTimeString(this.mediaTask.Duration),
          width: this.mediaTask.Width,
          height: this.mediaTask.Height
        }
      },

      convertProgress(progress) {
        this.nProgress = progress;
      },

      startConverter(){
        this.showCompress();
        this.state = 'convert';
        this.$refs.trimButton.click();
        this.convertProgressNum = 0
        this.nProgress = 0;
        this.mediaTask.trimStartTime= this.$refs.trimVue.trimStart();
        this.mediaTask.trimEndTime = this.$refs.trimVue.trimEnd();
        this.mediaTask.encodeParamsCheck = this.GLOBALFORMAT.g_EncodeParamsCheck;
        this.start_startTime = new Date().valueOf()
        this.mediaTask.startConverter(this.convertFinished, this.convertProgress);
      },

      convertFinished(files){
        if(files.length > 0) {
          this.draw();
          let file = files[0];
          this.outputFile = {
            name: file.name,
            src: file.src,
            fileSize: renderSize(file.fileSize)
          };
          this.state = 'finished';
        }
        else{
          this.$emit("ShowConvertFailInfo");
          this.cancelCallBack('confirm');
        }
      },
      downloadFinishedFile(){
          this.$refs.convertedFile.click();
      },

      onContinueClick(){
        this.reset();
        this.$emit("addNewFile")
      },
      cancelConverter() {
        if(this.state=='convert') {
          swal({
            text: 'Do you want to cancel this file? The changes won\'t be applied if you cancel this page?',
            buttons: {
              yes: {
                text: 'Yes'
              },
              no: {
                text: 'No'
              }
            }
          }).then(function(value){
            if(value == 'yes'){
              this.cancelCallBack('confirm')
            }
          }.bind(this))
        }
      },

      cancelCallBack(action){
        if (action === "confirm") {
          this.state = 'edit';
          this.nProgress = 0;
          this.mediaTask.CancelConvert();
        }
      },

      reset(){
        this.cropWithInitValue = 0;
        this.curTabPage = 'trimTab';
        this.converting = false;
        this.nProgress = 0;
        this.state = 'edit';
        this.videoDuration = 0;
        this.$refs.trimButton.click();
        if(this.isVideo) {
          this.$refs.adjustVue.resetAdjustValue();
          this.$refs.FlipVue.resetFlipValue();
          this.$refs.rotateVue.resetRotateValue();
          this.$refs.trimVue.resetTrimValue();
          this.$refs.cropControlVue.resetCropValue();
          this.$refs.cropVue.reset();
        }

        let canvasDiv = this.$refs.canvasDiv;
        canvasDiv.setAttribute('style', ' width: 640px; height: 360px');

        this.mediaTask.ResetCrop();
        this.hackReset = false;
        this.$nextTick(() => {
          this.hackReset = true
        })
      },
      resetEdit(){
        switch (this.curTabPage) {
          case 'trimTab':
            this.$refs.trimVue.resetTrimValue();
            break;
          case 'cropTab':
            this.ResetCrop();
            break;
          case 'adjustTab':
            this.$refs.adjustVue.resetAdjustValue();
            break;
          case 'rotateTab':
            this.$refs.rotateVue.resetRotateValue();
            break;
          case 'flipTab':
            this.$refs.FlipVue.resetFlipValue();
            break;
        }
      },
      ResetCrop(){
        // this.mediaTask.ResetCrop();
        this.$refs.cropVue.reset();
        // this.$refs.cropControlVue.resetCropValue();
      },
      resetClick(){
        console.log(this.$refs.adjustVue)
        if(this.curTabPage === "adjustTab"){
        this.$refs.adjustVue.resetAdjustValue();
        }
        else if(this.curTabPage === "flipTab") {
          this.$refs.FlipVue.resetFlipValue();
        }
        else if(this.curTabPage ==="rotateTab"){
          this.$refs.rotateVue.resetRotateValue();
        }
        else if(this.curTabPage ==="trimTab")
        {
          this.$refs.trimVue.resetTrimValue();
        }
        else if(this.curTabPage === "cropTab"){
          this.mediaTask.ResetCrop();
          this.$refs.cropControlVue.resetCropValue();
        }
      },
      draw() {
        this.mediaTask.render();
      },
      renderSize(obj){
        return renderSize(obj)
      },
      GetQueryString(name) {
        let reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
        let r = window.location.search.substr(1).match(reg);
        if (r != null) return unescape(r[2]);
        return null;
      },
      showCompress(){
        let editType = this.GetQueryString("type");
        return editType !== "VE";
      }
    }
  }
</script>

<style scoped>

  .nav-pills .nav-item {
    position: relative;
  }
  .nav-pills .nav-link {
    border: solid 1px #979797;border-radius: 10px;min-width: 100px;text-align: center;font-weight: 600;
  }
  .nav-pills .nav-link.active {
    border: solid 1px blueviolet;
  }
  .nav-pills .nav-link:hover {
    color: #fff;background-color: blueviolet;border: solid 1px blueviolet;
  }
  .reset-icon {
    background-color: #fff;position: absolute;display: block;width: 24px;height: 24px;line-height: 24px;border-radius: 24px;text-align: center;right:-4px;top: -8px;box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.49);
  }
  .reset-icon img {
    transition: all 0.4s linear;
  }
  .reset-icon:hover {
    box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.75);
  }
  .reset-icon:hover img{
    transform: rotate(-360deg);
  }
</style>



