<template>
  <div class="container bg-light text-center rounded p-4 mx-auto w-100 mt-2">
    <div v-if="state=='finished'">
      <img src="static/images/icon-success.svg" alt="">
      <h4 class="font-weight-bold text-info">Congratulations! Your file is ready</h4>
    </div>
    <div v-else-if="state=='convert'">
      <h4 class="font-weight-bold text-info">Preparing your file :</h4>
    </div>
    <div v-else>
      <div class="text-info"><h4 class="mediaName">{{mediaName}}</h4></div>
      <div class="row justify-content-center small text-left text-lg-center">
        <div class="col-lg-2 col-6">Duration: {{fileInfo.duration}}</div>
        <div class="col-lg-2 col-6">Size: {{fileInfo.size}}</div>
      </div>
    </div>


    <div class="row border rounded mt-2">
      <div class="col-lg-6 col-md-6 col-sm-12 mt-1 mb-1 py-2 pb-2">
       <img src="../assets/audioBk.png" style="max-width: 640px;max-height: 360px"/>
      </div>

      <div class="col-lg-6 col-md-6 col-sm-12">
        <div id="videoEditor" class="w-100 mt-5 pt-3 mb-5" v-show="state=='edit'">
          <converter :media-task="mediaTask" :edit-type="editType" ref="convertVue"/>
        </div>

        <div class="w-100 d-flex flex-column justify-content-center align-items-center mb-0 pt-3" style="height: 60% " v-if="state==='convert'">
          <div class="progress progress-striped w-75 m-3" >
            <div class="progress-bar progress-bar-info" v-bind:style="{'width': convertProgressNum+'%'}" role="progressbar" aria-valuenow="40" aria-valuemax="100" aria-valuemin="0"></div>
          </div>
          <div class="mt-2 font-weight-bold text-info">{{convertProgressNum}}% Complete</div>
        </div>

        <div v-show="state!='finished'">
          <a href="javascript:;" class="btn btn-info mx-2" @click="startConverter()" v-show="state!='convert'">Start Now</a>
          <a href="javascript:;" class="btn btn-outline-info mx-2" @click="onContinueClick()" v-show="state!='convert'">Cancel</a>
          <a href="javascript:;" v-if="state == 'convert'" class="btn btn-outline-info mx-2" @click="cancelConverter()">Cancel</a>
        </div>

        <div class="w-75 p-3 pl-5 mt-5 mb-3 text-center align-items-center justify-content-center border rounded" v-if="state=='finished'">
          <ul class="list-default">
            <li class="outputFileName">{{outputFile.name}}</li>
            <li>Format: {{this.mediaTask.OutFormat}}</li>
            <li>Size: {{ outputFile.fileSize || 'Original'}}</li>
            <li v-show="this.$refs.convertVue.editType_copy!=='AC'">Resolution: {{this.mediaTask.outputConfig.Resolution.Width + '*' + this.mediaTask.outputConfig.Resolution.Height}}</li>
            <li v-show="this.$refs.convertVue.editType_copy==='AC'">Audio Bitrate: {{(this.mediaTask.outputConfig.AudioBitrate || this.mediaTask.AudioBit) + ' kbps'}}</li>
          </ul>
        </div>

        <div class="w-75 py-1 mt-4 align-items-center justify-content-center"  v-show="state=='finished'">
          <a v-bind:href="outputFile.src" v-bind:download="outputFile.name" class="btn btn-info" @click="downloadButton()">Download Now</a>
          <a href="javascript:;" @click="onContinueClick()" class="btn btn-outline-info">Convert Another</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {GetDisplayTimeString, renderSize, getType} from "../js/util";
  import Converter from "./Converter";

  export default {
    name: "AudioConvert",
    components: { Converter},
    data() {
      return {
        mediaName: "",
        fileInfo:"",
        converting: false,
        nProgress: 0,
        state: 'edit', //edit, convert, finished
        videoDuration: 0,
        editType:'AC',
        convertProgressNum: 0,
        outputFile: {
          src: '',
          name: '',
          fileSize: ''
        },
        hackReset: true,
      }
    },
    props: ["mediaTask", "ShowConvertFailInfo"],

    mounted(){
       this.mediaTask.renderCanvas = this.$refs.imgCanvas;
     },

    computed: {
       imageThumb() {
        return this.mediaTask.imageThumb;
      },
      media() {
        return this.mediaTask.media;
      }
    },

    watch:{
      imageThumb(curVal, oldVal){
        if(curVal){
          this.draw();
        }
      },
      media(curVal,oldVal){
        if(curVal)
          this.mediaName = curVal.FileOrBlob.name;
      },
      nProgress(curVal,oldVal){
        if(curVal < oldVal) return;
        if(curVal <= 1){
          this.convertProgressNum = Math.floor(curVal*100)
        }
      },
    },
    methods: {
      fillFileInfo(){
        this.mediaName = this.mediaTask.media.FileOrBlob.name;
        this.videoDuration = this.mediaTask.media.MediaInfo.Duration;
        this.fileInfo = {
          fileType: getType(this.mediaName),
          size: renderSize(this.mediaTask.Size),
          duration: GetDisplayTimeString(this.mediaTask.Duration),
          width: this.mediaTask.Width,
          height: this.mediaTask.Height
        };
        this.$refs.convertVue.setDefaultFormatValue();
        if (this.mediaTask.OutFormat!=="MP3")
          this.mediaTask.OutFormat = "MP3";
        this.$refs.convertVue.audioBitValue="";
        this.mediaTask.outputAudioBit = 0;
      },

      convertProgress(progress) {
        this.nProgress = progress;
      },
      startConverter(){
        this.state = 'convert';
        this.convertProgressNum = 0
        this.nProgress = 0;
        this.mediaTask.encodeParamsCheck = this.GLOBALFORMAT.g_EncodeParamsCheck;
        this.mediaTask.startConverter(this.convertFinished, this.convertProgress);
        this.editType = "AC";
      },

      convertFinished(files){
        if(files.length > 0) {
          let file = files[0];
          this.outputFile = {
            name: file.name,
            src: file.src,
            fileSize: renderSize(file.fileSize)
          }
          this.state = 'finished';
        }
        else{
          this.$emit("ShowConvertFailInfo");
          this.cancelCallBack('confirm');
        }
      },
      downloadFinishedFile(){
          this.$refs.convertedFile.click();
      },
      onContinueClick(){
        this.reset();
        this.$emit("addNewFile");
      },
      cancelConverter() {
        if(this.state=='convert') {
          swal({
            text: 'Do you want to cancel this file? The changes won\'t be applied if you cancel this page?',
            buttons: {
              yes: {
                text: 'Yes'
              },
              no: {
                text: 'No'
              }
            }
          }).then(function(value){
            if(value == 'yes'){
              this.cancelCallBack('confirm')
            }
          }.bind(this))
        }else{
          this.onContinueClick();
        }
      },

      cancelCallBack(action){
        if (action === "confirm") {
          this.state = 'edit';
          this.nProgress = 0;
          this.mediaTask.CancelConvert();
        }
      },

      reset(){
        this.converting = false;
        this.nProgress = 0;
        this.state = 'edit';
        this.videoDuration = 0;
        this.hackReset = false
        this.$nextTick(() => {
          this.hackReset = true
        })
      },
      draw() {
        this.mediaTask.render();
      },
    }
  }
</script>



