<template>
  <div class="w-100">
    <div class="row w-100 m-0 justify-content-center align-items-center text-center py-2" >
      <div class="col-lg-2">Saturation</div>
      <div class="col-lg-8 mt-1"><range-slider :min="saturationMin" :max="saturationMax" v-model="saturation" v-on:change="slideChanged"></range-slider></div>
      <div class="col-lg-2"><i>{{saturation}}</i></div>
    </div>

    <div class="row w-100 m-0 justify-content-center align-items-center text-center py-2 " >
      <div class="col-lg-2">Contrast</div>
      <div class="col-lg-8 mt-1"><range-slider :min="contrastMin" :max="contrastMax" v-model="contrast" v-on:change="slideChanged"></range-slider></div>
      <div class="col-lg-2"><i>{{contrast}}</i></div>
    </div>

    <div class="row w-100 m-0 justify-content-center align-items-center text-center py-2" >
      <div class="col-lg-2">Brightness</div>
      <div class="col-lg-8 mt-1"><range-slider :min="brightnessMin" :max="brightnessMax" v-model="brightness" v-on:change="slideChanged"></range-slider></div>
      <div class="col-lg-2"><i>{{brightness}}</i></div>
    </div>
  </div>
</template>
<script>
     import RangeSlider from 'vue-range-slider'
    export default {
       name: "Adjust",
       data(){
          return{
            saturation: 0,
            saturationMin: -100,
            saturationMax: 100,
            contrast: 0,
            contrastMin: -100,
            contrastMax: 100,
            brightness: 0,
            brightnessMin: -100,
            brightnessMax: 100,
          }
        },
      props:["mediaTask"],
      components: {
        RangeSlider
      },
      methods:{
        slideChanged(){
          function adapterValue(value, medianValue){
            return value <=0 ? (100 + value) / 100 * medianValue : (value / 100) * (1- medianValue) + medianValue;
          }
          this.mediaTask.applyEffect(adapterValue(this.contrast, 0.5), adapterValue(this.saturation, 0.3), adapterValue(this.brightness, 0.5));
        },
        resetAdjustValue(){
          this.saturation = 0;
          this.contrast = 0;
          this.brightness = 0;
          this.slideChanged();
        }
      }
    }
</script>



