<template>
  <div id="app">
    <MainContent ref="editVue"/>
  </div>
</template>

<script>
import MainContent from "./components/MainContent"
import { guid } from "./js/util";
export default {
  name: 'App',
  data: () => ({
    show: true
  }),
  components: {
    MainContent
  },
  mounted() {
    if (!this.$cookies.get('oa')) {
      var obj = {
        guid: guid()
      }
      this.$cookies.set('oa', obj)
    }
  },
  methods:{
  }
}
</script>

<style>
</style>



