<template>
    <div class="justify-content-center">
      <div class="row py-2 m-3">
          <div class="col-lg-3 offset-2 text-left"> Output Format:</div>
          <div class="col-lg-4 ml-1 dropdown py-1 px-3 border border-gray97" v-if="editType == 'AC'">
            <a href="javascript:;" class="text-left dropdown-toggle" id="dropdownMenuButtonVA" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
              {{formatValue}}
            </a>
            <div class="dropdown-menu" aria-labelledby="dropdownMenuButtonVA" style="min-width: 190px">
              <a class="dropdown-item" v-for="item in this.GLOBALFORMAT.g_EncodeParamsCheck.getFomats('audio')" href="javascript:;"  @click="formatValue = item; editType_copy = 'AC'">{{item}}</a>
            </div>
          </div>

          <div class="col-lg-4 ml-1 dropdown py-1 px-3 border border-gray97" v-else>
            <a href="javascript:;" class="text-left dropdown-toggle" id="dropdownMenuButtonVF" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
              {{formatValue}}
            </a>
            <div class="dropdown-menu" aria-labelledby="dropdownMenuButtonVF" style="min-width: 190px">
              <a class="dropdown-item" v-for="item in this.GLOBALFORMAT.g_EncodeParamsCheck.getFomats('video')" href="javascript:;"  @click="formatValue = item;editType_copy = 'VC'">{{item}}</a>
            </div>
          </div>
      </div>
      <div class="row py-2 m-3" v-if="editType_copy == 'AC'">
          <div class="col-lg-3 offset-2 text-left">Audio Bitrate:</div>
          <div class="col-lg-4 ml-1 dropdown py-1 px-3 border border-gray97">
            <a href="javascript:;" class="text-left dropdown-toggle" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
              {{audioBitValue ? audioBitValue + ' kbps' : 'Same as Source'}}
            </a>
            <div class="dropdown-menu" aria-labelledby="dropdownMenuButton" style="min-width: 190px">
              <a class="dropdown-item" v-for="item in getAudioBits()" href="javascript:;" @click="audioBitValue = item.value">{{item.label}}</a>
            </div>
          </div>
      </div>
      <div class="row py-2 m-3" v-else>
          <div class="col-lg-3 offset-2 text-left" >Resolution:</div>
          <div class="col-lg-4 dropdown ml-1 py-1 px-3 border border-gray97">
            <a href="javascript:;" class="text-left dropdown-toggle" id="dropdownMenuButtonV" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
              {{resolutionValue || 'Same as Source'}}
            </a>
            <div class="dropdown-menu" aria-labelledby="dropdownMenuButtonV" style="min-width: 190px">
              <a class="dropdown-item" v-for="item in getResolutions()" href="javascript:;" @click="resolutionValue = item">{{item || 'Same as Source'}}</a>
            </div>
          </div>
      </div>
    </div>
</template>
<script>
    export default {
        name: "Converter",
        data() {
          return {
            resolutionValue: null,
            audioBitValue: '',
            formatValue: 'MP4',
            editType_copy: "VC"
          };
      },
      created(){
        this.editType_copy = this.editType;
        this.setDefaultFormatValue();
      },
        props: ["mediaTask","editType"],
        watch:{
          resolutionValue(curVal, oldVal){
            this.mediaTask.resulotionValue = curVal;
          },
          formatValue(curVal, oldVal){
            this.mediaTask.OutFormat = curVal;
          },
          audioBitValue(curVal, oldVal) {
            this.mediaTask.outputAudioBit = curVal;
          },
          editType(curVal,oldVal) {
            this.setDefaultFormatValue();
          }
        },
      methods:{
        getAudioBits(){
            // return this.GLOBALFORMAT.AudioBitRates;
            let arr = this.GLOBALFORMAT.AudioBitRates.map(function(value,index,array){ return {'label': value + ' kbps', 'value': value}; });
            arr.push({'label': 'Same as Source', 'value': null});
            arr.reverse();
             console.log(arr)
            return arr;
          },
        getResolutions(){
          let arr = this.GLOBALFORMAT.g_EncodeParamsCheck.getResolutionList(this.formatValue)
          arr.unshift(null);
          [0].concat(arr);
          return arr;
        },
        getFormatType(){
          if(this.editType==='AC' || this.editType ==='ACP') {
            return "audio";
          }else{
            return "video";
          }
        },
        GetQueryString(name) {
          let reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
          let r = window.location.search.substr(1).match(reg);
          if (r != null) return unescape(r[2]);
          return null;
        },
        setDefaultFormatValue() {
          debugger
          let format = this.GetQueryString("format");
          this.formatValue = format ? format : this.getFormatType() === "audio" ? "MP3" : "MP4";
        }
      }
    }
</script>

<style scoped>
  .labelnormalcss {
    font-size: 1rem;
    text-align: left;
    margin-right: 30px;
    width: 18%;
  }
  .selectcss{
    width: 60%;
  }
  .dropdown, .dropright {
    min-width: 100px;
  }
  .dropdown-toggle:hover {
    text-decoration: none;
  }
  .dropdown .dropdown-menu {
    width: 100%;transform: none !important;top: 100% !important;border-radius: 0;min-width: 0;
  }
  .dropdown-item:hover {
    background-color: #02367d;color: #fff;
  }
</style>



