<template>
    <div>
      <div class="d-flex justify-content-center">
        <div class="mx-4  d-flex align-items-center">
          <span>Left: </span>
          <a href="javascript:;" v-on:click="btnleftRoate(-90)"><img class="hover-hide" src="../assets/left.svg" alt=""><img class="hover-show" src="../assets/left-hover.svg" alt=""></a>
        </div>
        <div class="mx-4 d-flex align-items-center">
          <span>Right: </span>
          <a href="javascript:;" v-on:click="btnleftRoate(90)"><img class="hover-hide" src="../assets/right.svg" alt=""><img class="hover-show" src="../assets/right-hover.svg" alt=""></a>
        </div>
      </div>
    </div>
</template>

<script>
    export default {
        name: "Rotate",
        data(){
          return{
            angle: 0,
            angleEx: 0,
            title: 'this is rotate component!'
          }
        },
      props:["mediaTask"],
      methods: {
        btnleftRoate(value) {
          this.angle += value;
          this.angleEx = this.angle % 360;
          this.mediaTask.applyRotateEffect(this.angleEx);
          //this.render();
        },
        resetRotateValue(){
          this.angle = 0;
          this.mediaTask.applyRotateEffect(this.angle);
        },

        render(){
          let videoCanvas = this.mediaTask.renderCanvas;
          if (videoCanvas.getContext('2d')) {
            let  ctx = videoCanvas.getContext('2d');
            let  img = new Image();
            let self = this;
            img.onload = function () {
              // 图片原始尺寸
              let originWidth = img.width;
              let originHeight = img.height;
              // if (self.firstImageThumb) {
              //   self.firstImageThumb = false;
              //   self.previewWidth = self.cropWidth = img.width;
              //   self.previewHeight = self.cropHeight = img.height;
              // }
              // 最大尺寸限制，可通过国设置宽高来实现图片压缩程度
               let maxWidth = 640, maxHeight = 360;

              //设置父div的位置
              //videoCanvas.parentElement.style.left = (maxWidth - originWidth) / 2 + 'px';
              videoCanvas.parentElement.style.top = (maxHeight - originHeight) / 2 + 'px';
              // canvas对图片进行缩放
              videoCanvas.width = originWidth;
              videoCanvas.height = originHeight;
              videoCanvas.style.width = originWidth + 'px';
              videoCanvas.style.height = originHeight + 'px';
              // 清除画布
              if (self.angleEx % 360 !== 0) {
                ctx.clearRect(0, 0, videoCanvas.width, videoCanvas.height);
                let xPos = videoCanvas.width / 2;
                let yPos = videoCanvas.height / 2;
                ctx.save();
                let scale = 0;
                if(self.angleEx % 180 !== 0) {
                  scale = videoCanvas.height / img.width;
                  if (img.height > img.width)
                    scale = videoCanvas.width / img.height;
                  ctx.scale(scale, scale);
                }
                ctx.translate(xPos, yPos);
                ctx.rotate(self.angleEx * Math.PI / 180);
                ctx.translate(-xPos, -yPos);
                console.log("self.angle =============" + self.angleEx)
                if(scale!==0){
                  if (img.height > img.width)
                    ctx.drawImage(img, xPos - img.width / 2, yPos - img.height / 2);
                  else
                    if(self.angleEx == -270 || self.angleEx == 90)
                      ctx.drawImage(img, xPos - img.height / 2, yPos - img.width / 2);
                    else
                      ctx.drawImage(img, yPos - img.width / 2, xPos - img.height / 2);
                }
                else {
                  ctx.drawImage(img, xPos - img.width / 2 , yPos - img.height / 2 );
                }
                ctx.restore();
              } else {
                ctx.drawImage(img, 0, 0);
              }
            };
            img.src = this.mediaTask.cropImageBlob? this.mediaTask.cropImageBlob: this.mediaTask.imageThumb.src;
          }
        }
      }
    }
</script>

<style scoped>
  .button{
    width: 100px;
    height: 100px;
  }
</style>



