<template>
  <div>
    <div class="mt-5 d-flex flex-column flex-lg-row justify-content-center align-items-center">
      <div class="m-2">Aspect ratio:</div>
      <div class="dropdown py-1 px-3 border border-gray97">
        <a href="javascript:;" class="dropdown-toggle" style="min-width: 100px;" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
          {{resolution.text}}
        </a>
        <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
          <a class="dropdown-item" v-for="item in resolution_list" href="javascript:;" @click="resolution = item">{{item.text}}</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    import CropControl from './CropControl'
    export default {
        name: "Crop",
        data(){
          return {
            title: 'this is crop component!',
            resetCropArea: false,
            resolution: {
                ratio: 'default',
                text: 'Customized'
              },
            resolution_list: [
              {
                ratio: 'default',
                text: 'Customized'
              },
              {
                ratio: 1,
                text: '1:1 (square)'
              },
              {
                ratio: 1.333,
                text: '4:3 (classic computer)'
              },
              {
                ratio: 1.777,
                text: '16:9 (wide screen)'
              }
            ]
          }
        },
      props:["mediaTask"],
      watch: {
        resolution: function(currVal,oldVal){
           if((currVal.ratio === oldVal.ratio) &&  !this.resetCropArea) return;
          this.resetCropArea =  false;
          this.CancelCrop();
          this.mediaTask.isCroping = true;
          this.$emit('child-say', currVal.ratio);
          if(currVal.ratio ==="default")
            return;
          let divEle = document.getElementById('cropArea');
          let w = this.mediaTask.previewWidth;//this.mediaTask.renderCanvas.clientWidth;
          let h = this.mediaTask.previewHeight;//this.mediaTask.renderCanvas.clientHeight;
          if(w/h > currVal.ratio){
            divEle.style.width =h * currVal.ratio/w*100+'%'
          }else{
            divEle.style.height = w/currVal.ratio/h*100+'%'
          }
          let currentSizeEle = document.getElementById('currentSize');
          if(currentSizeEle){
            currentSizeEle.innerText = divEle.offsetWidth + "x" + divEle.offsetHeight;
          }
          CropControl.cropTop = divEle.offsetTop;
          CropControl.cropLeft = divEle.offsetLeft;
          CropControl.cropWidth = divEle.offsetWidth;
          CropControl.cropHeight = divEle.offsetHeight;
        }
      },
      methods:{
        CancelCrop(){
          // debugger
          let divEle = document.getElementById('cropArea');
          divEle.style.width= '100%';
          divEle.style.height='100%';
          divEle.style.top= '0';
          divEle.style.left= '0';
          divEle.style.visibility = "";
          var currentSizeEle = document.getElementById('currentSize');
          if(currentSizeEle){
            currentSizeEle.innerText = divEle.offsetWidth + "x" + divEle.offsetHeight;
          }
          this.mediaTask.ResetCrop();
          // this.$emit("CropCanceled",'');
        },
        reset(){
          this.resetCropArea =  true;
          this.resolution = {
            ratio: 'default',
            text: 'Customized'
          }
        }
      },
    }
</script>

<style scoped>
.dropdown-toggle:hover {
  text-decoration: none;
}
.dropdown .dropdown-menu {
  min-width: 100%;transform: none !important;top: 100% !important;border-radius: 0;min-width: 0;
}
.dropdown-item:hover {
  background-color: #02367d;color: #fff;
}
</style>



