<template>
  <div>
    <div class="bg-light rounded p-4 mx-auto text-center" style="max-width: 1200px; " v-show="!isLoading">
      <label class="addfilepanelcss py-4 d-block" style="max-width: 1200px;" v-if="!isLoading" v-on:drop="drop($event)" v-on:dragover="dragover($event);droping = true" v-on:dragleave="droping = false">
        <input type="file" class="position-absolute d-none" v-on:change="addFile($event)">
        <div id="addIcon" class="position-relative">
          <img src="../assets/add.svg" />
          <img class="addIcon-icon" src="../assets/add-icon.svg" alt="">
        </div>
      </label>
      <div class="px-4">
        <h6 class="mb-4">{{label1}}</h6>
      </div>
    </div>
    <div v-if="isLoading">
      <div class="bg-light rounded p-4 mx-auto d-flex align-items-center justify-content-center" style="max-width: 1200px;min-height: 300px;">
        <div class="w-100" style="max-width: 400px;">
          <h6 class="mb-4">File Loading</h6>
            <div class="progress progress-striped w-100" >
              <div class="progress-bar progress-bar-info" v-bind:style="{'width': fileLoadProgress+'%'}" role="progressbar" aria-valuenow="40" aria-valuemax="100" aria-valuemin="0">{{fileLoadProgress}}%</div>
            </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
  import {getType} from "../js/util";
  export default {
    name: "FileLoad",
    data() {
      return {
        isLoading: false,
        title: "Add your media file to convert",
        label1: "Drag & drop or select files from computer",
        label2: "Drop media convert to video",
        fileLoadProgress: 0,
        droping: false,
        // typeEdit: {
        //   DEF:['Video Converter', 'Video Editor', 'Video Compressor', 'Audio Converter'],
        //   VC: ['MP4 Converter', 'MPE Converter', 'iPhone Converter', 'Audio Converter'],
        //   VE: ['Video Editor for YouTube', 'Android Video Editor', 'Instagram Video Editor', 'MP4 Video Editor'],
        //   VCP: ['MP4 Compressor', 'MOV Compressor', 'Compress Video for Email', 'Compress Video for YouTube'],
        //   AC: ['MP3 Converter', 'WAV Converter', 'M4A Converter', 'Audio Converter']
        // }

      }
    },
    props: ["mediaTask","editType", "ShowLoadFailInfo"],
    watch:{
      editType(curVal){
        switch (curVal) {
          case 'VC':
            this.title = "Add your media file to convert";
            this.label1 = "Create my video";
            this.label2 = "Drop media convert to video";
            break;
          case 'VE':
            this.title = "Add your media file to edit";
            this.label1 = "Edit my video";
            this.label2 = "Drop media file edit and convert to video";
            break;
          case 'VTrim':
            this.title = "Add your media file to trim";
            this.label1 = "Trim my video";
            this.label2 = "Drop media file trim and convert to video";
            break;
          case 'VAdjust':
            this.title = "Add your media file to adjust";
            this.label1 = "Adjust my video";
            this.label2 = "Drop media file adjust and convert to video";
            break;
          case "VRF":
            this.title = "Add your media file to rotate or flip";
            this.label1 = "Rotate or Flip and flip  my video";
            this.label2 = "Drop media file rotate or flip and convert to video";
            break;
          case 'VCP':
            this.title = "Add your video to compress";
            this.label1 = "Compress my video";
            this.label2 = "Drop video to compress";
            break;
          case 'AC':
            this.title = "Add your file to convert";
            this.label1 = "Create my audio";
            this.label2 = "Drop media convert to audio";
            break;
          case 'ACP':
            this.title = "Add your audio  to compress";
            this.label1 = "Compress my audio";
            this.label2 = "Drop audio to compress";
            break;
        }
      }
    },
    mounted() {
      if(!this.editType) this.editType = "VC";
    },
    methods:{
      addFile(e){
        let theEvent = e || window.event
        if(theEvent.target.value == '' || theEvent.target.value == null){
          return false
        }
        this.checkFile(theEvent.target.files[0])
        theEvent.target.value = ''
      },
      checkFile(file) {
        if (this.checkFormatSupport(file)){
          this.doLoadFile(file)
        }
      },
      checkFormatSupport(fileName){
        // debugger
        let filter = [];
        switch (this.editType) {
          case 'VC':
            filter = this.GLOBALFORMAT.videoFormatFilter;
            break;
          case 'VE':
          case 'VTrim':
          case 'VAdjust':
          case "VRF":
            filter = this.GLOBALFORMAT.videoFormatFilter;
            break;
          case 'VCP':
            filter = this.GLOBALFORMAT.videoFormatFilter;
            break;
          case 'AC':
            filter = this.GLOBALFORMAT.MediaFormatFilter;
            break;
          case 'ACP':
            filter = this.GLOBALFORMAT.audioFormatFilter;
            break;
        }
        if(filter.indexOf(getType(fileName.name))> -1) {
          return true;
        }else{
          this.$emit("ShowLoadFailInfo");
          return false;
        }
      },
      async doLoadFile(file)
      {
        this.isLoading = true;
        this.mediaTask.loadFile(file, true)
        let fileLoadInterval = setInterval(function(){
          if(this.fileLoadProgress < 90){
            this.fileLoadProgress += 10
          }else{
            clearInterval(fileLoadInterval)
          }
        }.bind(this),200)
      },
      drop(e){
        let theEvent = e || window.event
        theEvent.preventDefault()
        this.checkFile(theEvent.dataTransfer.files[0])
      },
      dragover(e){
        let theEvent = e || window.event
        theEvent.preventDefault()
        this.droping = true
      },
      reset(){
        this.isLoading = false;
        this.fileLoadProgress = 0
      },

      GetQueryString(name) {
        let reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
        let r = window.location.search.substr(1).match(reg);
        if (r != null) return unescape(r[2]);
        return null;
      },
    }
  }
</script>

<style scoped>
  .addfilepanelcss{
    cursor: pointer;
  }
  #addIcon .addIcon-icon {
    transition: all 0.4s linear;position: absolute;left: 50%;top: 50%;transform: translate(-50%, -50%);
  }
  #addIcon:hover .addIcon-icon {
    transform: translate(-50%, -50%) rotate(180deg);
  }
</style>



