<template>
  <div class="container bg-light text-center rounded p-4 mt-2 mx-auto w-100">
    <div v-if="state=='finished'">
      <img src="static/images/icon-success.svg" alt="">
      <h4 class="font-weight-bold text-info">Congratulations! Your file is ready</h4>
    </div>
    <div v-else-if="state=='convert'">
      <h4 class="font-weight-bold text-info">Preparing your file:</h4>
    </div>
    <div v-else>
      <div class="text-info"><h4 class="mediaName">{{mediaName}}</h4></div>
      <div class="row justify-content-center small text-left text-lg-center">
        <div class="col-lg-2 col-6">Duration: {{fileInfo.duration}}</div>
        <div class="col-lg-2 col-6">Size: {{fileInfo.size}}</div>
        <div class="col-lg-2 col-12">Resolution: {{fileInfo.width + 'x' + fileInfo.height}}</div>
      </div>
    </div>

    <div class="row border rounded mt-2">
      <!--缩略图区域-->
      <div class="col-lg-6 col-md-6 col-sm-12 mt-1 mb-1 py-2 pb-2">
        <div class="canvasDiv" ref="canvasDiv">
          <div class="d-inline-block" style="display:table-cell; vertical-align: middle; text-align: center;">
            <div class="position-relative w-100" style="font-size: 0;left:0 !important; text-align:center;vertical-align: middle; display: inline-block;" v-show="isVideo">
              <canvas id="imageCanvas" ref="imgCanvas"></canvas>
            </div>
          </div>
        </div>
      </div>

      <div class="col-lg-6 col-md-6 col-sm-12">
        <!--转换完成信息-->
        <div class="w-75 p-3 pl-5 mt-5 mb-3 text-center align-items-center justify-content-center border border-gray97 rounded" v-if="state=='finished'">
          <ul class="list-default">
            <li class="outputFileName">{{outputFile.name}}</li>
            <li>Format: {{this.mediaTask.OutFormat}}</li>
            <li>Size: {{ outputFile.fileSize || 'Original'}}</li>
            <li>Resolution: {{this.mediaTask.outputConfig.Resolution.Width + '*' + this.mediaTask.outputConfig.Resolution.Height}}</li>
          </ul>
        </div>
        <!--转换进度 -->
        <div class="w-100 d-flex flex-column justify-content-center align-items-center mb-0 pt-3" style="height: 60% " v-if="state==='convert'">
          <div class="progress progress-striped w-75 m-3" >
            <div class="progress-bar progress-bar-info" v-bind:style="{'width': convertProgressNum+'%'}" role="progressbar" aria-valuenow="40" aria-valuemax="100" aria-valuemin="0"></div>
          </div>
          <div class="mt-2 font-weight-bold text-info">{{convertProgressNum}}% Complete</div>
        </div>
        <!--编辑区域 -->
        <!--rotate工具-->
        <div class="w-100 mt-1 pt-2 mb-1"  v-show="state=='edit'">
          <Rotate :media-task="mediaTask" ref="rotateVue"/>
        </div>
        <!--flip工具-->
        <div class="w-100 mt-1 pt-2 mb-3"  v-show="state=='edit'">
          <Flip :media-task="mediaTask" ref="FlipVue" />
        </div>
        <!--重置按钮-->
        <div class="row w-100 pt-2"  v-if="state!='finished'">
          <a href="javascript:;" class="col-lg-2 col-md-6 col-sm-4 offset-lg-5-10 offset-md-5 offset-sm-4 nav-link" @click="resetEdit()" v-show="state=='edit'">Reset</a>
        </div>
        <!--转换设置区域-->
        <div  class="w-100 mt-3 pt-2 mb-4" v-show="state=='edit'">
          <ConvertSet :media-task="mediaTask" v-if="hackReset" :edit-type="editType" ref="convertVue"/>
        </div>
        <!--控制按钮-->
        <div v-if="state!='finished'">
          <a href="javascript:;" class="btn btn-info mx-2" @click="startConverter()" v-show="state!='convert'">Start Now</a>
          <a href="javascript:;" class="btn btn-outline-info mx-2" @click="onContinueClick()" v-show="state!='convert'">Cancel</a>
          <a href="javascript:;" v-if="state == 'convert'" class="btn btn-outline-info mx-2" @click="cancelConverter()">Cancel</a>
        </div>
        <!--完成控制按钮-->
        <div class="w-75 py-1 mt-4 align-items-center justify-content-center" v-show="state=='finished'">
          <a v-bind:href="outputFile.src" v-bind:download="outputFile.name" class="btn btn-info m-2"><span>Download Now</span></a>
          <a href="javascript:;" @click="onContinueClick()" class="btn btn-outline-info m-2">Convert Another</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {GetDisplayTimeString, renderSize, getType} from "../js/util";
  import Rotate from "./Rotate";
  import Flip from "./Flip";
  import ConvertSet from "./ConvertSet";

  export default {
    name: "VideoRotate",
    components: {ConvertSet, Flip, Rotate},
    data() {
      return {
        mediaName: "",
        fileInfo: "",
        converting: false,
        nProgress: 0,
        state: 'edit', //edit, convert, finished
        videoDuration: 0,
        isVideo: true,
        convertProgressNum: 0,
        outputFile: {
          src: '',
          name: '',
          fileSize: ''
        },
        hackReset: true,
      }
    },
    props: ["mediaTask", "ShowConvertFailInfo", "editType"],
    mounted() {
      this.mediaTask.renderCanvas = this.$refs.imgCanvas;
    },

    computed: {
      canvasWidth: function () {
        return this.$refs.imgCanvas.clientWidth;
      },
      imageThumb() {
        return this.mediaTask.imageThumb;
      },
      media() {
        return this.mediaTask.media;
      }
    },

    watch: {
      imageThumb(curVal, oldVal) {
        if (curVal) {
          this.draw();
        }
      },
      media(curVal, oldVal) {
        if (curVal)
          this.mediaName = curVal.FileOrBlob.name;
      },
      nProgress(curVal, oldVal) {
        if (curVal < oldVal) return;
        if (curVal <= 1) {
          this.convertProgressNum = Math.floor(curVal * 100)
        }
      },
    },
    methods: {
      fillFileInfo() {
        this.mediaName = this.mediaTask.FileName;
        this.videoDuration = this.mediaTask.Duration;
        this.fileInfo = {
          fileType: getType(this.mediaName),
          size: renderSize(this.mediaTask.Size),
          duration: GetDisplayTimeString(this.mediaTask.Duration),
          width: this.mediaTask.Width,
          height: this.mediaTask.Height
        }
      },

      convertProgress(progress) {
        this.nProgress = progress;
      },
      startConverter() {
        this.state = 'convert';
        this.convertProgressNum = 0
        this.nProgress = 0;
        this.mediaTask.encodeParamsCheck = this.GLOBALFORMAT.g_EncodeParamsCheck;
        this.mediaTask.startConverter(this.convertFinished, this.convertProgress);
      },

      convertFinished(files) {
        if (files.length > 0) {
          let file = files[0];
          this.outputFile = {
            name: file.name,
            src: file.src,
            fileSize: renderSize(file.fileSize)
          };
          this.state = 'finished';
        } else {
          this.$emit("ShowConvertFailInfo");
          this.cancelCallBack('confirm');
        }
      },
      downloadFinishedFile() {
        this.$refs.convertedFile.click();
      },

      onContinueClick() {
        this.reset();
        this.$emit("addNewFile")
      },
      cancelConverter() {
        if (this.state == 'convert') {
          swal({
            text: 'Do you want to cancel this file? The changes won\'t be applied if you cancel this page?',
            buttons: {
              yes: {
                text: 'Yes'
              },
              no: {
                text: 'No'
              }
            }
          }).then(function (value) {
            if (value == 'yes') {
              this.cancelCallBack('confirm')
            }
          }.bind(this))
        }
      },

      cancelCallBack(action) {
        if (action === "confirm") {
          this.state = 'edit';
          this.nProgress = 0;
          this.mediaTask.CancelConvert();
        }
      },
      reset() {
        this.converting = false;
        this.nProgress = 0;
        this.state = 'edit';
        this.videoDuration = 0;
        this.$refs.FlipVue.resetFlipValue();
        this.$refs.rotateVue.resetRotateValue();
        let canvasDiv = this.$refs.canvasDiv;
        canvasDiv.setAttribute('style', ' width: 640px; height: 360px');
        this.hackReset = false;
        this.$nextTick(() => {
          this.hackReset = true
        })
      },
      resetEdit() {
        this.$refs.rotateVue.resetRotateValue();
        this.$refs.FlipVue.resetFlipValue();
      },
      draw() {
        this.mediaTask.render();
      },
      renderSize(obj) {
        return renderSize(obj)
      },
    }
  }
</script>

<style scoped>

  .nav-pills .nav-item {
    position: relative;
  }
  .nav-link {
    border: solid 1px #979797;border-radius: 10px;min-width: 100px;text-align: center;font-weight: 600;
  }
  .nav-link.active {
    border: solid 1px blueviolet;
  }
  .nav-link:hover {
    color: #fff;background-color: blueviolet;border: solid 1px blueviolet;
  }
  .reset-icon {
    background-color: #fff;position: absolute;display: block;width: 24px;height: 24px;line-height: 24px;border-radius: 24px;text-align: center;right:-4px;top: -8px;box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.49);
  }
  .reset-icon img {
    transition: all 0.4s linear;
  }
  .reset-icon:hover {
    box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.75);
  }
  .reset-icon:hover img{
    transform: rotate(-360deg);
  }
</style>



